import React from "react";
import { Accordion, Card, Button } from "react-bootstrap";

function Faq() {
    const caret = <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" class="feather feather-chevron-down"><polyline points="6 9 12 15 18 9"></polyline></svg>;
    return (
        <React.Fragment>
            <div className="container mb-5 mt-5 animate__animated animate__fadeInLeft">
                <div className="row">
                    <div className="col">
                        <h5 className="faq-h5 text-center">FAQS</h5>
                        <h2 className="faq-h2 text-center">Any <span className="heading-color">Questions?</span></h2>
                        <h5 className="faq-p text-center">And we have got answers to all of them. Lorem ipsum dolor sit amet, consectetur adipiscing elit.</h5>
                        <div className="accordian-container">
                            <Accordion defaultActiveKey="null">
                                <Card className="accordian-card">
                                    <Card.Header>
                                            <Accordion.Toggle as={Button} variant="link" eventKey="0" className="panel-header">
                                                <div className="accordian-header">Is lunch provided free of cost ? <span className="accordian-caret-0">{caret}</span></div>
                                            </Accordion.Toggle>
                                    </Card.Header>
                                    <Accordion.Collapse eventKey="0">
                                    <Card.Body><div className="accordian-body">Yes, it is, if you have a membership with us. Otherwise it is charged as per the menu. Some limits do apply as to how much items can be included in your lunch. This limit is enough for any one person and merely exists to discourage abusal of the system.</div></Card.Body>
                                    </Accordion.Collapse>
                                    </Card>
                                <Card className="accordian-card">
                                    <Card.Header>
                                        <Accordion.Toggle as={Button} variant="link" eventKey="1" className="panel-header">
                                            <div className="accordian-header">Do you have 2 Bedroom suites ? <span className="accordian-caret-1">{caret}</span></div>
                                        </Accordion.Toggle>
                                    </Card.Header>
                                    <Accordion.Collapse eventKey="1">
                                    <Card.Body><div className="accordian-body">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.</div></Card.Body>
                                    </Accordion.Collapse>
                                </Card>
                                <Card className="accordian-card">
                                    <Card.Header>
                                        <Accordion.Toggle as={Button} variant="link" eventKey="2" className="panel-header">
                                            <div className="accordian-header">Are Wi-Fi costs included in the price ? <span className="accordian-caret-2">{caret}</span></div>
                                        </Accordion.Toggle>
                                    </Card.Header>
                                    <Accordion.Collapse eventKey="2">
                                    <Card.Body><div className="accordian-body">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.</div></Card.Body>
                                    </Accordion.Collapse>
                                </Card>
                                <Card className="accordian-card">
                                    <Card.Header>
                                        <Accordion.Toggle as={Button} variant="link" eventKey="3" className="panel-header">
                                            <div className="accordian-header">Where can I reach you for support ? <span className="accordian-caret-3">{caret}</span></div>
                                        </Accordion.Toggle>
                                    </Card.Header>
                                    <Accordion.Collapse eventKey="3">
                                    <Card.Body><div className="accordian-body">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.</div></Card.Body>
                                    </Accordion.Collapse>
                                </Card>
                            </Accordion>
                        </div>
                    </div>
                    </div>
            </div>
        </React.Fragment>
    );
}

export default Faq;